body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.add-task-button {
	width:60%;
	padding: 5px;
	background: green;
	color: white;
	font-size: 35px;
	margin: 15px 0;
	border: none;
	position: fixed;	
	top: 0;
	z-index: 10;
	box-shadow: 0px 1px 5px #7d7d7d;
}
.task {
	border: 1px solid black;
	position: relative;
	padding:10px;
	border-radius:2px;
	margin: 5px;
	box-sizing: border-box;
	transition: all 100ms;
	min-height: 56px;
	color: black;
}

.task.hovered:before {
	height: 2px;
	position: absolute;
	left: -10px;
	top:-7px;
	width: calc(100% + 20px);
	background: blue;
	content:'';
}

.priority {
	position: absolute;
	left:0;
	top:0;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	z-index: 2;
}

.redRadio {
	background: red;
}

.yellowRadio {
	background: yellow;
}

.greenRadio {
	background: green;
}


.blueRadio {
	background: blue;
}

.purpleRadio {
	background: purple;
}

.red {
	background: #ffa5a5;
}

.yellow {
	background: #fff9a5;
}

.green {
	background: #bbffa5;
}

.blue {
	background: #a5e1ff;
}

.purple {
	background: #eb98ff;
}

input[type="radio"] {
	-webkit-appearance: none;
	   -moz-appearance: none;
	        appearance: none;
	width: 20px;
	height: 20px;
	border-radius: 50%;
	margin: 19px 10px;
	box-shadow: 0 0 5px 1px black;
}

input[type="radio"]:focus {
	outline: none;
}

.delete {
	position: absolute;
	top: 0;
	right: 0;
	height:30px;
	width: 30px;
	padding: 10px;
	font-size: 25px;
	cursor: pointer;
	color: red;
	text-shadow: 0px 0px 20px white;
	z-index:2;
}

.edit {
	padding: 10px;
	font-size: 20px;
	position: absolute;	
	top:0;
	left:0;	
	cursor:pointer;
	z-index:2;
}

.section-title {
	float: left;
	font-weight: bold;
	margin-right: 10px;
}

.section-data {
	

}

.section {
	width: 100%;
	float: left;
	margin: 5px 0;
}

.title {
	margin-top: 0px;
	text-align: center;
	margin-bottom: 5px;
	margin-left: 250px;
	margin-right: 50px;
	text-align: left;
}

.note {
	margin-bottom: 30px;
}

.cover {
	width:100%;
	height: 100%;
	position: absolute;
	top:0;
	left: 0;
	right: 0;
	bottom: 0;
	z-index:1;
}
.edit-window {
	position: fixed;
	width: 30%;
	height: 100%;
	top:0;
	right:0;
	border-left: 1px solid black;
	background: transparent;
	z-index: 5;
}

.dark .edit-window {
	border-color: white;
}

.formSection {
	padding: 10px;
}

.formSection input {
	padding:10px;
	width:90%;
	border: 1px solid black;
	border-radius: 2px;
	font-size: 16px;
	letter-spacing: 1px;
	width: 100%;
	box-sizing: border-box;
}

.formSection textarea {
	box-sizing: border-box;
}

.formSection label {
	display: block;
}

.play-pause {
	width: 0;
	height: 0;
}

.play-pause.playing {
	width: 10px;
	height: 30px;
	border-left: 10px solid blue;
	border-right: 10px solid blue;
}

.play-pause.paused {
	border: 15px solid transparent;
	border-left-color: blue;
	margin-left: 10px;
}

.outer {
	width:100vw;
	height:100vh;
	left:0;
	right:0;
	bottom:0;
	top: 0;
	background: #8080809e;
	position: fixed;
	z-index: 4;
}

.close-button {
	position: absolute;
	top:0;
	right:0;
	width:20px;
	height:20px;
	text-align: center;
	background:red;
	padding: 5px;
	color: white;
	cursor: pointer;
}

#note {
	width:100%;
	float:left;
	height:200px;
	font-size: 16px;
	font-family: inherit;
}
.App {
  width: 100%;
  margin: 0;
  min-height: 100vh;

}

.main-container {
  padding:80px 35% 5% 5%;
}

.dark {
  background: black;
  color: white;
}

.dark-mode-control {
  display: -webkit-flex;
  display: flex;
}

.dark-mode-control.dark {
  color: white;
}

.toggle {
  -moz-appearance: none;
       appearance: none;
  -webkit-appearance: none;
  width: 35px;
  height: 22px;
  border: 2px solid #636363;
  border-radius: 11px;
  box-sizing: border-box;
  position: relative;
  background: #d8d8d8;
}
  
.toggle:after {
  content: '';
  width: 18px;
  height: 18px;
  border-radius: 9px;
  background: green;
  display: block;
  position: absolute;
  left: -1px;
  right: auto;
  transition: all 400ms;

}

.toggle.on {
  background: white;
}

.toggle.on:after {
    right: 0;
    left: auto;
}

.toggle:before {
  content: 'OFF';
  position: absolute;
  left: 120%;
}

.toggle.on:before {
  content: "ON";
  color: white;
}

@media only screen and (max-width: 748px) {
  .App {
    grid-template-columns:1fr;
  }
}
